export default {
	props: {
		isSearch: {
			type: Boolean,
			default: false
		},
		word: {
			type: [String, Number],
			default: ''
		}
	},
	data() {
		return {
			statusBarHeight: 0,
			navBarHeight: 45,
			navBarWidth: 375
		}
	},
	created() {
		const res = uni.getSystemInfoSync()
		this.statusBarHeight = res.statusBarHeight
		this.navBarWidth = res.windowWidth

		const capsule = uni.getMenuButtonBoundingClientRect()
		// 导航栏高度=（胶囊底部坐标-状态栏坐标）+（胶囊顶部坐标-状态栏坐标）
		this.navBarHeight = (capsule.bottom - res.statusBarHeight) + (capsule.top - res.statusBarHeight)
		// 导航栏宽度=胶囊左边坐标
		this.navBarWidth = capsule.left

	},
	methods: {
		toSearchPage() {
			uni.navigateTo({
				url: '../../../pages/search/search',
				fail: function(err) {
					console.log(err)
				},
				success: function() {
					// this.isSearch = !this.isSearch
				}
			})
		},
		inputChange(e) {
			this.$emit('searchInput', e.detail.value)
		},
		back() {
			uni.switchTab({
				url: "../../pages/tabbar/index/index",
				fail(err) {
					cosnole.log(err)
				}
			})
		}

	}
}
